.inventory-item {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 8px;
    padding: 15px;
    margin: 10px;
    display: flex;
    align-items: center;
    transition: transform 0.2s;
    width: 300px;
}

.inventory-item:hover {
    transform: translateY(-2px);
    background: rgba(255, 255, 255, 0.15);
}

.item-image {
    width: 100px;
    height: 100px;
    object-fit: contain;
    margin-right: 15px;
}

.item-info {
    flex: 1;
}

.item-name {
    color: #fff;
    margin: 0 0 8px 0;
    font-size: 16px;
}

.item-price {
    color: #aaa;
    margin: 0 0 12px 0;
    font-size: 14px;
}

.sell-button {
    background: #4CAF50;
    color: white;
    border: none;
    padding: 8px 16px;
    border-radius: 4px;
    cursor: pointer;
    transition: background 0.2s;
}

.sell-button:hover {
    background: #45a049;
}

.sell-button:disabled {
    background: #cccccc;
    cursor: not-allowed;
} 