.inventory-container {
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
}

.inventory-title {
    color: #fff;
    text-align: center;
    margin-bottom: 30px;
    font-size: 24px;
}

.inventory-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
    justify-items: center;
}

.inventory-loading {
    color: #fff;
    text-align: center;
    font-size: 18px;
    margin-top: 50px;
}

.inventory-error {
    color: #ff4444;
    text-align: center;
    font-size: 18px;
    margin-top: 50px;
} 